import React from 'react';
import {Badge, Navbar, NavbarBrand} from 'reactstrap';

export const PageHeader = ({totalNumberOfAnswers, totalNumberOfCorrectAnswers, numberOfAuthorsLeft}) => {
  return <Navbar>
    <NavbarBrand> </NavbarBrand>
    {totalNumberOfAnswers > 0 && <div className={'text-right'}>
      <Badge color={'primary'} className={'mr-2'}>: {totalNumberOfAnswers}</Badge>
      <Badge color={'success'} className={'mr-2'}>: {totalNumberOfCorrectAnswers}</Badge>
      <Badge color={'info'}> : {numberOfAuthorsLeft}</Badge>
    </div>}
  </Navbar>;
};



